.tube__items {
  margin-top: 55px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.tube__item {
  box-shadow: 0px 12px 17.85px 3.15px rgba(0, 0, 0, 0.1);
  width: 25%;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  border-right: 2px solid #f2f3f5;
}

.tube__item:last-child {
  border-right: none;
}

@media screen and (max-width: 1024px) {
  .tube__item {
    width: 50%;
    margin-bottom: 20px;
  }
}

@media screen and (max-width: 425px) {
  .tube__item {
    width: 100%;
  }
}

.tube__img-cont {
  width: 100%;
  height: 190px;
  display: flex;
  transition: 0.3s;
}

.tube__img-cont:hover {
  opacity: 0.7;
}

.tube__content {
  padding: 30px 20px 80px 20px;
}

.tube__title {
  font-size: 24px;
  font-family: "Segoe UI";
  color: black;
  line-height: 1.875;
  font-weight: 500;
}

.tube__title:hover {
  text-decoration: underline;
}

.tube__desc {
  font-size: 18px;
  font-family: "Segoe UI";
  color: #676767;
  line-height: 2.5;
}
